import React from "react";
import Counter from "../../../utils/ui/Counter";

const Number = () => {
    const numbers = [
        { value: 1000, text: " " },
        { value: 10, text: " " },
        { value: 2019, text: "" },
    ];

    return (
        <section className="flex flex-row justify-between py-40 px-16">
            {numbers.map((number) => (
                <Counter number={number.value} title={number.text} />
            ))}
        </section>
    );
};

export default Number;
